import React from "react";
import * as firebase from "firebase";
import "./App.css";
import ListItems from "./listItems";
import UserCre from "./userCre";

class App extends React.Component {
  constructor(props) {
    super(props);
    this.state = {
      shahmen: "Shahmen is Waitin!",
      status: "",
      headerstyle: {},
      loadingItems: false,
      Added: false,
      user: null,
      loginCheck: false,
      items: [],
      currentItem: {
        text: null,
        key: null,
      },
    };

    this.handleInput = this.handleInput.bind(this);
    this.addItem = this.addItem.bind(this);
    this.deleteItem = this.deleteItem.bind(this);
    this.logout = this.logout.bind(this);
    this.saveItems = this.saveItems.bind(this);
    this.getItems = this.getItems.bind(this);
  }
  // This function converts the "this.state.items" array into 2 layer object
  reducer(acc, cur) {
    return { ...acc, [cur.text]: cur };
  }
  logout() {
    console.log(this.state.items);
    firebase.auth().signOut();
    this.setState({
      items: [],
      status: "",
    });
  }
  saveItems() {
    // This statement converts the "this.state.items" array into 2 layer object
    let dualObj = this.state.items.reduce(this.reducer, {});
    const fire = firebase.firestore();
    const user = firebase.auth().currentUser;
    console.log("User items (save): ", dualObj);
    fire.collection("USERDATA").doc(user.uid).set(dualObj);
    const ref = fire.collection("users");
    ref.doc(user.uid).set({
      email: user.email,
    });
    /*
    for (var i = 0; i < this.state.items.length; i++) {
      console.log("User items: ", this.state.items[i]);
      fire.collection("USERDATA").doc(user.uid).set(this.state.items[i]);
    }*/
    this.setState({
      status: "saved!",
    });
    setTimeout(() => {
      this.setState({
        status: "",
      });
    }, 1500);
  }

  getItems() {
    this.setState({
      loadingItems: true,
    });
    setTimeout(() => {
      const fire = firebase.firestore();
      const user = firebase.auth().currentUser;
      console.log("original: ", this.state.items);
      fire
        .collection("USERDATA")
        .doc(user.uid)
        .get()
        .then((doc) => {
          const obj = doc.data();
          console.log("obj " + obj);
          if (obj !== undefined) {
            console.log("retrived :", obj);
            let conv = Object.keys(obj).reduce((array, key) => {
              return [...array, obj[key]];
            }, []);
            console.log("O to A: ", conv);
            this.setState({
              items: conv,
            });
          }
        });
      this.setState({
        loadingItems: false,
      });
    }, 1000);
  }
  authListener() {
    firebase.auth().onAuthStateChanged((user) => {
      //console.log(user);
      if (user) {
        this.setState({
          headerstyle: {
            float: "left",
            textAlign: "",
          },
        });
        this.setState({
          shahmen: "Shahmen is Watchin!",
        });
        this.setState({ user: true });
        this.getItems();
      } else {
        this.setState({ user: null });
        this.setState({
          shahmen: "Shahmen is Waitin!",
        });
        this.setState({
          headerstyle: {},
        });
      }
    });
  }
  handleInput(e) {
    e.preventDefault();
    this.setState({
      currentItem: {
        text: e.target.value,
        key: Date.now(),
      },
    });
  }
  addItem(e) {
    this.setState({
      Added: true,
    });
    e.preventDefault();
    const newItem = this.state.currentItem;
    console.log(newItem);
    if (newItem.text !== null) {
      const newItems = [...this.state.items, newItem];
      this.setState({
        items: newItems,
        currentItem: {
          text: null,
          key: null,
        },
      });
    }
    setTimeout(() => {
      this.setState({
        Added: false,
      });
    }, 2000);
  }
  deleteItem(key) {
    const filteredItems = this.state.items.filter((item) => item.key !== key);
    this.setState({
      items: filteredItems,
    });
  }
  componentDidMount() {
    this.authListener();
  }
  render() {
    let replaceDev;
    if (this.state.user) {
      replaceDev = (
        <ListItems
          items={this.state.items}
          deleteItem={this.deleteItem}
          onSubmit={this.addItem}
          value={this.state.currentItem.text}
          onChange={this.handleInput}
          loadingItems={this.state.loadingItems}
          Added={this.state.Added}
        ></ListItems>
      );
    } else {
      replaceDev = <UserCre />;
    }
    return (
      <div>
        <header id="header">
          <div id="headersort">
            <h3 style={this.state.headerstyle}>{this.state.shahmen}</h3>
            <div>
              {this.state.user ? (
                <div className="HBtn">
                  <p class="status">{this.state.status}</p>
                  <button className="logoutBTN" onClick={this.logout}>
                    Logout
                  </button>
                  <button className="saveBTN" onClick={this.saveItems}>
                    Save
                  </button>
                </div>
              ) : (
                <div></div>
              )}
            </div>
          </div>
        </header>
        <div>{replaceDev}</div>
        <div className="footer">
          <p>
            All copy rights goes to <i>HM Muneeb UR rehman</i>
          </p>
        </div>
      </div>
    );
  }
}

export default App;
