import React, { Fragment, version } from "react";
import Loading_main from "./assets/loading.svg";
import loading_small_black from "./assets/loading_small_black.svg";
import "./loading_spinner.css";

function Loader(props) {
  const version = props.version;
  return (
    <Fragment>
      {version === "Main" ? (
        <img src={Loading_main} id="main" alt="loading" />
      ) : (
        <img
          src={loading_small_black}
          id="loading_small_black"
          alt="loading"
          height="15"
        />
      )}
    </Fragment>
  );
}
export default Loader;
