import React, { Component } from "react";
import "./userCre.css";
import * as firebase from "firebase";
import Loader from "./loading_spinner";
class userCre extends Component {
  constructor(props) {
    super(props);
    this.state = {
      status: "Login",
      loading: false,
      currentUser: "",
      email: "",
      password: "",
      error: "",
    };
    this.login = this.login.bind(this);
    this.EmailHandleChange = this.EmailHandleChange.bind(this);
    this.PassHandleChange = this.PassHandleChange.bind(this);
    this.login = this.login.bind(this);
    this.signUpBtn = this.signUpBtn.bind(this);
    this.BackBtn = this.BackBtn.bind(this);
    this.createUser = this.createUser.bind(this);
  }

  createUser(e) {
    e.preventDefault();
    var errorCode;
    var errorMessage;
    console.log("error code: " + errorCode);
    firebase
      .auth()
      .createUserWithEmailAndPassword(this.state.email, this.state.password)
      .catch(function (error) {
        // Handle Errors here.
        errorCode = error.code;
        errorMessage = error.message;
        console.log("error code: ", errorCode);
        console.log("error msg: ", errorMessage);
      })
      .then((u) => {
        if (errorCode !== undefined) {
          this.setState({
            currentUser: this.state.email,
            status: "Login",
            email: "",
            password: "",
            error: "",
          });
        }
      });
    if (errorCode === "auth/weak-password") {
      this.setState({
        error: "Password must be at least 6 characters long!",
      });
    } else {
      this.setState({
        error: "Invalid email or password entered!",
      });
    }
  }

  login(e) {
    this.setState({
      loading: true,
    });
    e.preventDefault();
    firebase
      .auth()
      .signInWithEmailAndPassword(this.state.email, this.state.password)
      .then((u) => {
        this.setState({
          currentUser: this.state.email,
        });
      })
      .catch((error) => {
        console.log("current user: (error)", this.state.currentUser);
        console.log(error);
        if (error.code === "auth/wrong-password") {
          this.setState({
            error: "Wrong password!",
          });
        } else {
          this.setState({
            error: "Invalid email or password!",
          });
        }
      });
    this.setState({
      loading: false,
    });
  }
  signUpBtn(e) {
    e.preventDefault();
    this.setState({
      status: "Sign up",
      email: "",
      password: "",
      error: "",
    });
  }
  BackBtn(e) {
    e.preventDefault();
    this.setState({
      status: "Login",
      email: "",
      password: "",
      error: "",
    });
  }

  EmailHandleChange(e) {
    this.setState({ email: e.target.value });
  }
  PassHandleChange(e) {
    this.setState({ password: e.target.value });
  }
  poppSignUp(e) {
    e.preventDefault();
  }
  render() {
    let replace;
    if (this.state.status === "Login") {
      replace = (
        <div>
          <div className="userLog">
            <form className="form" id="signInform">
              <h1>Sign In</h1>
              <div className="InputField">
                <label>Email:</label>
                <input
                  value={this.state.email}
                  onChange={this.EmailHandleChange}
                  type="text"
                  name="email"
                  placeholder="Enter email here..."
                />
              </div>
              <div className="InputField">
                <label>Password:</label>
                <input
                  value={this.state.password}
                  onChange={this.PassHandleChange}
                  type="password"
                  name="pass"
                  placeholder="Enter password"
                />
              </div>
              <div className="btn">
                <button type="submit" id="login" onClick={this.login}>
                  {this.state.loading ? (
                    <Loader version="Loading_small_black" />
                  ) : (
                    " "
                  )}
                  {this.state.status}
                </button>
              </div>
              <p id="errorMessage">{this.state.error}</p>
            </form>
            <center>
              <div className="userNote">
                <p>
                  Not a Member?
                  <br />
                  <a onClick={this.signUpBtn}>SignUp</a>
                </p>
              </div>
            </center>
          </div>
        </div>
      );
    }
    if (this.state.status === "Sign up") {
      replace = (
        <div>
          <div className="userLog">
            <a id="back" onClick={this.BackBtn}>
              Back
            </a>
            <form className="form">
              <h1>Sign Up</h1>
              <div className="InputField">
                <label>Email:</label>
                <input
                  value={this.state.email}
                  onChange={this.EmailHandleChange}
                  type="text"
                  name="email"
                  placeholder="Enter email here..."
                />
              </div>
              <div className="InputField">
                <label>Password:</label>
                <input
                  value={this.state.password}
                  onChange={this.PassHandleChange}
                  type="password"
                  name="pass"
                  placeholder="Enter password"
                />
              </div>
              <div className="btn">
                <button type="submit" id="SignUp" onClick={this.createUser}>
                  {this.state.loading ? (
                    <Loader version="Loading_small_black" />
                  ) : (
                    " "
                  )}
                  {this.state.status}
                </button>
              </div>
              <p id="errorMessage">{this.state.error}</p>
            </form>
            <center>
              <div className="userNote">
                <p>
                  By signing up you are agreeing to all the user terms and
                  servives
                </p>
              </div>
            </center>
          </div>
        </div>
      );
    } else {
    }
    return <div>{replace}</div>;
  }
}
export default userCre;
